import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { NpcNewDTO } from 'src/app/models/Npc/npc_new.dto';
import { NpcDTO } from 'src/app/models/Npc/npc.dto';
import { StorageService } from '../../storage-service.service';
import { NpcUpdateDTO } from 'src/app/models/Npc/npc-update.dto';
import { NpcBasicUpdateDTO } from 'src/app/models/Npc/npc-basic-update.dto';
import { GenericHallDTO } from 'src/app/models/hall/generic-hall.dto';

@Injectable({
  providedIn: 'root'
})
export class NpcPageService {

  constructor(private http: HttpClient, private storage: StorageService) { }


  findPageByNewer(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/npc/newer`,
      {
        params,
        observe: 'body',
      });
  }

  findPageBySearch(page: string = '0', descricao: string, orderBy: string, direction: string, linesPerPage: string = '10'): Observable<any> {
    console.log('D: ', direction);
    const params = new HttpParams()
      .set('page', page)
      .set('descricao', descricao)
      .set('orderBy', orderBy)
      .set('direction', direction)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/npc/search`,
      {
        params,
        observe: 'body',
      });
  }
}
